footer {
  background: -webkit-gradient(linear,left top, right bottom,from(#f7f7f7),to(#eafbff));
  background: -webkit-linear-gradient(top left,#f7f7f7,#eafbff);
  background: -o-linear-gradient(top left,#f7f7f7,#eafbff);
  background: linear-gradient(to bottom right,#f7f7f7,#eafbff);
  padding: 2.5em 1em;
  height: 10vh;
  color: #525c65;
}

footer .made-with,
footer .icons {
  text-align: center;
  color:#525c65;
  margin: 0;
}

footer .icons {
  font-size: 14px;
}

footer .made-with img {
  height: 22px;
  margin-bottom: -5px;
  margin-left: 10px;
}

footer .made-with a,
footer .icons a {
  display: inline-block;
  text-decoration: none;
  color: #7d97ad;
  padding-bottom: 5px;
  border-bottom: 0;
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

footer .made-with a:after,
footer .icons a:after {
  display: block;
  content: '';
  border-bottom: solid 1px #7d97ad;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: 0% 50%;
      -ms-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.4s ease-in-out;
  transition: -webkit-transform 0.4s ease-in-out;
  -o-transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}

footer .made-with a:hover:after,
footer .made-with a:focus:after,
footer .icons a:hover:after,
footer .icons a:focus:after {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
}

@media (max-width: 769px) {
  footer {
    height: 100%;
  }
}




